<template>
  <v-btn
    class="save mr-3"
    :class="{ 'disable-events': isDisabled }"
    color="#4D6C99"
    ref="save"
    small
    dark
    depressed
    @click="saveData"
    :ripple="false"
  >
    Сохранить
  </v-btn>
</template>

<script>
export default {
  name: 'SaveButton',
  data() {
    return {
      isDisabled: true
    };
  },
  methods: {
    saveData() {
      let btnCancel = document.querySelector('.cancel');
      let btnSave = document.querySelector('.save');
      btnCancel.classList.add('disable-events');
      btnSave.classList.add('disable-events');
    }
  }
};
</script>

<style scoped lang="sass">
@font-face
  font-family: FSRAILBook
  src: url('../assets/fonts/FSRAILWAYBook.otf') format('opentype')

button.save
  font-family: 'FSRAILBook', serif
  width: 120px
  font-size: 14px
  line-height: 17px
  letter-spacing: 0.01em
  text-transform: none
.v-btn:not(.v-btn--round).v-size--small
  height: 29px

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
