<template>
  <v-app>
    <v-navigation-drawer
      id="nav"
      :mini-variant="mini"
      color="#00194D"
      app
      permanent
      :width="300"
      class="nav-bold-text"
      floating
      mini-variant-width="80"
    >
      <div @click="home">
        <img class="ma-4" :src="require(`@/assets/images/${changeLogo}`)" />
      </div>
      <v-list v-if="!mini" dark dense flat>
        <template v-for="item in this.$store.state.menuItems">
          <v-list-group
            v-if="item.children"
            v-model="item.model"
            active-class="is-active"
            color="#FFFFFF"
            :key="item.text"
            :prepend-icon="item.icon"
            :ripple="false"
            @click="closeChildren(item)"
          >
            <template v-slot:activator>
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </template>

            <template v-for="child in item.children">
              <v-list-group
                v-if="child.children"
                sub-group
                v-model="child.model"
                class="pa-0"
                color="#ff7141"
                :key="child.text"
                :prepend-icon="child.icon"
                :ripple="false"
              >
                <template v-slot:activator>
                  <v-list-item-title class="mr-0">
                    {{ child.text }}
                  </v-list-item-title>
                </template>

                <v-list-item
                  v-for="(sub_child, i) in child.children"
                  v-model="sub_child.model"
                  class="plain-text ml-12 py-0 pl-3 my-0 rounded-l-lg"
                  active-class="is-active is-last-nested"
                  color="#00194d"
                  :key="i"
                  :to="{ path: sub_child.path }"
                  :ripple="false"
                >
                  <v-list-item-title class="text-wrap">
                    {{ sub_child.text }}
                  </v-list-item-title>
                </v-list-item>
              </v-list-group>
              <v-list-item
                v-else
                v-model="child.model"
                class="ml-5 rounded-l-lg"
                active-class="is-active"
                color="#FF7141"
                :key="child.text"
                :to="{ path: child.path }"
                :ripple="false"
              >
                <v-list-item-action class="mr-2 my-0">
                  <v-icon>{{ child.icon }}</v-icon>
                </v-list-item-action>
                <v-list-item-content class="py-0">
                  <v-list-item-title>
                    {{ child.text }}
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </template>
          </v-list-group>
          <v-list-item
            v-else
            v-model="item.model"
            color="#FF7141"
            :key="item.text"
            :to="{ path: item.path }"
            :class="mini ? 'rounded-circle my-2' : 'rounded-l-lg'"
            :active-class="mini ? 'active-list-mini' : 'active-list'"
            :prepend-icon="item.icon"
            :ripple="false"
          >
            <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-2 my-0'">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content class="py-0">
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
      <v-list v-else dark dense flat>
        <template v-for="item in this.$store.state.menuItems">
          <v-list-item
            v-if="item.isMiniView"
            v-model="item.model"
            color="#FF7141"
            :key="item.text"
            :to="{ path: item.path }"
            :class="mini ? 'rounded-circle my-2' : 'rounded-l-lg'"
            :active-class="mini ? 'active-list-mini' : 'active-list'"
            :prepend-icon="item.icon"
            :ripple="false"
            @click="setChildPath(item)"
          >
            <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-2 my-0'">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content class="py-0">
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <template v-for="child in item.children">
            <v-list-item
              v-if="child.isMiniView"
              v-model="child.model"
              color="#FF7141"
              :key="child.text"
              :to="{ path: child.path }"
              :class="mini ? 'rounded-circle my-2' : 'rounded-l-lg'"
              :active-class="mini ? 'active-list-mini' : 'active-list'"
              :prepend-icon="child.icon"
              :ripple="false"
              @click="setChildPath(child)"
            >
              <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-2 my-0'">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content class="py-0">
                <v-list-item-title>
                  {{ child.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
        </template>
      </v-list>
      <MenuFooter />
    </v-navigation-drawer>
    <v-main id="main">
      <v-btn class="toggle-btn" @click="toggle" color="#00194d" dark small>
        <v-icon>{{ appMenuIcon }}</v-icon>
      </v-btn>

      <router-view></router-view>
      <Footer />
    </v-main>
  </v-app>
</template>

<script>
import Footer from './components/Footer';
import MenuFooter from './components/MenuFooter';
export default {
  name: 'App',

  components: { MenuFooter, Footer },

  data: () => {
    return {
      mini: false
    };
  },
  created() {
    // this.$router.push('/control/norm/section');
  },
  methods: {
    home() {
      this.$router.push('/');
    },
    setChildPath(item) {
      if (item.children) {
        this.$router.push(item.children[0].path);
      }
    },
    toggle() {
      this.$store.state.menuMini = this.mini = !this.mini;
    },
    closeChildren(item) {
      if (!this.mini) {
        this.$store.state.menuItems.forEach((parentElement) => {
          if (parentElement.path === item.path && parentElement.children) {
            parentElement.children.forEach((childElement) => {
              childElement.model = false;
            });
          }
        });
      }
    }
  },
  computed: {
    changeLogo() {
      return this.mini ? 'logo-mini.svg' : 'logo.svg';
    },
    appMenuIcon() {
      return this.mini ? 'mdi-chevron-right' : 'mdi-chevron-left';
    }
  }
};
</script>

<style>
/* doesn't work in sass => need to find variable? */
.v-list-group__header__prepend-icon i {
  transform: none !important;
}
</style>

<style scoped lang="sass">
