  font-family: 'FSRAILBook'
  src: local('FSRAILBook'), url('assets/fonts/FSRAILWAYBook.otf') format('opentype')
@font-face
  font-family: 'FSRAILBold'
  src: local('FSRAILBold'), url('assets/fonts/FSRAILWAYBold.otf') format('opentype')

.plain-text div
  font-family: $body-font-family
  font-size: $font-size-root !important
  line-height: 18px

.nav-bold-text
  font-family: $font-bold
  color: $color-dark-blue
  border-radius: 0 5px 5px 0

#main
  .toggle-btn
    background-color: $color-dark-blue
    position: absolute
    left: -5px
    top: 50%
    width: 10px
    min-width: 0

.active-list-mini
  background-color: $color-light-grey
  margin-left: 24px
  width: 30px

.v-list
  &-item
    transition: color $transition-normal
    #nav &
      min-height: 30px
    &.is-active
      cursor: default
      &.is-last-nested
        background-color: $color-light-grey
    .v-icon
      transition: color $transition-normal
    &__title.mr-0:after
      content: '\F0140'
      font: $font-material
      position: absolute
      top: 9px
      right: 23px
      transition: transform $transition-normal
      .v-list-group--sub-group.v-list-group--active &
        transform: rotate(-180deg)
  .theme--dark
    &.v-list-item:not(.is-active):hover
      color: $color-orange !important
      .v-icon
        color: $color-orange !important
</style>
