<template>
  <v-btn
    class="cancel mr-auto"
    :class="{ 'disable-events': isDisabled }"
    color="#FF7141"
    ref="cancel"
    small
    dark
    depressed
    @click="cancelChange"
    :ripple="false"
  >
    Отмена
  </v-btn>
</template>

<script>
export default {
  name: 'CancelButton',
  data() {
    return {
      isDisabled: true
    };
  },
  methods: {
    cancelChange() {
      let btnRecalc = document.querySelector('.recalc');
      let btnSave = document.querySelector('.save');
      let btnCancel = document.querySelector('.cancel');
      btnRecalc.classList.add('disable-events');
      btnSave.classList.add('disable-events');
      btnCancel.classList.add('disable-events');
    }
  }
};
</script>

<style scoped lang="sass">
@font-face
  font-family: FSRAILBook
  src: url('../assets/fonts/FSRAILWAYBook.otf') format('opentype')

button.cancel
  font-family: 'FSRAILBook', serif
  width: 120px
  font-size: 14px
  line-height: 17px
  letter-spacing: 0.01em
  text-transform: none
.v-btn:not(.v-btn--round).v-size--small
  height: 29px

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
