<template>
  <div class="pa-3">
    <v-expansion-panels v-model="panel" focusable hover>
      <v-expansion-panel>
        <v-expansion-panel-header
          @click="changeFilter()"
          hide-actions
          :color="filterHeaderColor"
          class="pa-1 pl-2 pt-2"
          :ripple="false"
        >
          <div>
            <v-icon class="mr-2" color="white">mdi-filter-outline</v-icon>
            <span>Фильтры</span>
            <v-icon class="ml-2" color="white">{{ filterIcon }}</v-icon>
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row dense>
            <DateFilter v-on:change-date="getDate" ref="dateFilter" />
          </v-row>
          <FilterButtons />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row class="ml-1 title-row">
      <h3 class="mr-5 block-title">
        <v-icon color="#ff7141" class="mr-2">mdi-trending-up</v-icon>Оперативное
        нормирование
      </h3>
      <RecalcButton />
      <SaveButton />
      <CancelButton />
      <PrintButton />
    </v-row>
    <Tabs :tableTypeArray="tablesForTabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="getParams"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import DataTable from '../components/DataTable';
import DateFilter from '../components/DateFilter';
import PrintButton from '../components/PrintButton';
import RecalcButton from '../components/RecalcButton';
import SaveButton from '../components/SaveButton';
import CancelButton from '../components/CancelButton';
import FilterButtons from '../components/FilterButtons';
import Tabs from '../components/Tabs';
export default {
  name: 'NormalizationRatio',
  components: {
    Tabs,
    FilterButtons,
    CancelButton,
    SaveButton,
    RecalcButton,
    PrintButton,
    DateFilter,
    DataTable
  },
  watch: {
    dateValue() {
      this.$refs.table.fetchTableData(this.getParams);
    }
  },
  methods: {
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    },
    getDate(dateFilter) {
      this.dateValue = dateFilter;
    }
  },
  computed: {
    filterIcon() {
      return this.$store.state.filterOpen
        ? 'mdi-chevron-up'
        : 'mdi-chevron-down';
    },
    filterHeaderColor() {
      return this.$store.state.filterOpen ? '#4D6C99' : '#30446E';
    },
    getParams() {
      let dateToParams = this.dateValue;
      if (typeof this.dateValue === 'string') {
        let dateFromFilter = new Date(this.dateValue);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return { date: dateToParams, drl: 1 };
    }
  },
  data() {
    return {
      panel: 0,
      params: {},
      dateValue: Math.floor(Date.now() / 1000),
      dateFilterId: 'dateNormRatio',
      url: '../../server/norm_minmax.json',
      tableType: 'norm_ratio',
      tablesForTabs: [
        { tabName: 'Участки', tablePath: '/control/norm/section' },
        { tabName: 'Депо', tablePath: '/control/norm/depo' },
        {
          tabName: 'Процентное соотношение локомотивов',
          tablePath: '/control/norm/ratio'
        }
      ],
      tabModel: 2,
      headersFlat: [
        { text: 'Депо приписки локомотивов', value: 'depo' },
        { text: 'Т (4-х), т.ед.', value: 'park_t4', editable: true },
        { text: 'Т (3-х), т.ед.', value: 'park_t3', editable: true },
        { text: 'Л (2-х), т.ед.', value: 'park_l2', editable: true },
        { text: 'Т (4-х)', value: 'neisp_t4', editable: true },
        { text: 'Т (3-х)', value: 'neisp_t3', editable: true },
        { text: 'Л (2-х)', value: 'neisp_l2', editable: true },
        { text: 'Т (4-х), т.ед.', value: 'max_t4' },
        { text: 'Т (3-х), т.ед.', value: 'max_t3' },
        { text: 'Л (2-х), т.ед.', value: 'max_l2' },
        { text: 'Т (4-х)', value: 'uch_t4' },
        { text: 'Т (3-х)', value: 'uch_t3' },
        { text: 'Л (2-х)', value: 'uch_l2' }
      ],
      headers: [
        {
          text: 'Депо приписки локомотивов',
          align: 'start',
          value: 'depo',
          textMinify: 'mini'
        },
        {
          text: 'Инвентарный парк',
          align: 'start',
          alignHeader: 'center',
          cols: 3,
          children: [
            { text: 'Т (4-х), т.ед.', value: 'park_t4', alignHeader: 'center' },
            { text: 'Т (3-х), т.ед.', value: 'park_t3', alignHeader: 'center' },
            { text: 'Л (2-х), т.ед.', value: 'park_l2', alignHeader: 'center' }
          ]
        },
        {
          text: 'Плановая доля неисправных локомотивов',
          align: 'start',
          alignHeader: 'center',
          cols: 3,
          children: [
            { text: 'Т (4-х)', value: 'neisp_t4', alignHeader: 'center' },
            { text: 'Т (3-х)', value: 'neisp_t3', alignHeader: 'center' },
            { text: 'Л (2-х)', value: 'neisp_l2', alignHeader: 'center' }
          ]
        },
        {
          text: 'Макс. содержание',
          align: 'start',
          alignHeader: 'center',
          cols: 3,
          children: [
            { text: 'Т (4-х), т.ед.', value: 'max_t4', alignHeader: 'center' },
            { text: 'Т (3-х), т.ед.', value: 'max_t3', alignHeader: 'center' },
            { text: 'Л (2-х), т.ед.', value: 'max_l2', alignHeader: 'center' }
          ]
        },
        {
          text: '% содержания от общего по участку',
          align: 'start',
          alignHeader: 'center',
          cols: 3,
          children: [
            { text: 'Т (4-х)', value: 'uch_t4', alignHeader: 'center' },
            { text: 'Т (3-х)', value: 'uch_t3', alignHeader: 'center' },
            { text: 'Л (2-х)', value: 'uch_l2', alignHeader: 'center' }
          ]
        }
      ]
    };
  }
};
</script>
<style scoped lang="sass">
.v-expansion-panel-header
  color: white
  line-height: 16px
  border-radius: 5px
.title-row
  margin-top: 30px
  margin-bottom: 30px
.block-title
  font-family: 'FSRAILBold', serif
  font-size: 18px
  margin-top: 2px
  line-height: 21px
  color: #00194d
</style>
