<template>
  <div class="pa-3">
    <v-expansion-panels v-model="panel" focusable hover>
      <v-expansion-panel>
        <v-expansion-panel-header
          @click="changeFilter()"
          hide-actions
          :color="filterHeaderColor"
          class="pa-1 pl-2 pt-2"
          :ripple="false"
        >
          <div>
            <v-icon class="mr-2" color="white">mdi-filter-outline</v-icon>
            <span>Фильтры</span>
            <v-icon class="ml-2" color="white">{{ filterIcon }}</v-icon>
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row dense>
            <DateFilter v-on:change-date="getDate" ref="dateFilter" />
          </v-row>
          <FilterButtons />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row class="ml-1 title-row">
      <h3 class="mr-auto block-title">
        <v-icon color="#ff7141" class="mr-2">mdi-cog-outline</v-icon>Оперативное
        регулирование
      </h3>
      <PrintButton />
      <ExportButton :url="createExportUrl" />
    </v-row>
    <Tabs :tableTypeArray="tablesForTabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="getParams"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import DataTable from '../components/DataTable';
import DateFilter from '../components/DateFilter';
import PrintButton from '../components/PrintButton';
import ExportButton from '../components/ExportButton';
import FilterButtons from '../components/FilterButtons';
import Tabs from '../components/Tabs';
export default {
  name: 'RegulationJoints',
  components: {
    Tabs,
    FilterButtons,
    PrintButton,
    DateFilter,
    DataTable,
    ExportButton
  },
  watch: {
    dateValue() {
      this.$refs.table.fetchTableData(this.getParams);
    }
  },
  methods: {
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    },
    getDate(dateFilter) {
      this.dateValue = dateFilter;
    }
  },
  computed: {
    filterIcon() {
      return this.$store.state.filterOpen
        ? 'mdi-chevron-up'
        : 'mdi-chevron-down';
    },
    filterHeaderColor() {
      return this.$store.state.filterOpen ? '#4D6C99' : '#30446E';
    },
    getParams() {
      return { date: this.dateValue };
    },
    createExportUrl() {
      return 'method=regulation&tab=region-view&date=' + this.getParams.date;
    }
  },
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      dateValue: Math.floor(Date.now() / 1000),
      url: '../../server/splices.json',
      tableType: 'reg_join',
      tabModel: 0,
      tablesForTabs: [
        { tabName: 'Стыки', tablePath: '/control/reg/joint' },
        { tabName: 'Участки', tablePath: '/control/reg/section' },
        {
          tabName: 'Анализ',
          tablePath: '/control/reg/analysis'
        }
      ],
      headersFlat: [
        { text: '№ п/п', value: 'sort_number' },
        { text: 'Междорожные стыковые пункты', value: 'long_name' },
        { text: 'на 6:00 ч', value: 'plan_train_half', editable: true },
        { text: 'на 18:00 ч', value: 'plan_train', editable: true },
        { text: 'на 6:00 ч', value: 'plan_loco_half' },
        { text: 'на 18:00 ч', value: 'plan_loco' },
        { text: 'на 6:00 ч', value: 'plan_train_loco_half' },
        { text: 'на 18:00 ч', value: 'plan_train_loco' }
      ],
      headers: [
        {
          text: '№ п/п',
          align: 'start',
          value: 'sort_number',
          textMinify: 'mini'
        },
        {
          text: 'Междорожные стыковые пункты',
          align: 'start',
          value: 'long_name',
          textMinify: 'mini'
        },
        {
          text: 'План-задание передачи поездов',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'на 6:00 ч',
              value: 'plan_train_half',
              alignHeader: 'center'
            },
            { text: 'на 18:00 ч', value: 'plan_train', alignHeader: 'center' }
          ]
        },
        {
          text: 'План-задание передачи локомотивов',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'на 6:00 ч',
              value: 'plan_loco_half',
              alignHeader: 'center'
            },
            { text: 'на 18:00 ч', value: 'plan_loco', alignHeader: 'center' }
          ]
        },
        {
          text: 'План-задание передачи поездо-локомотивов',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'на 6:00 ч',
              value: 'plan_train_loco_half',
              alignHeader: 'center'
            },
            {
              text: 'на 18:00 ч',
              value: 'plan_train_loco',
              alignHeader: 'center'
            }
          ]
        }
      ]
    };
  }
};
</script>

<style scoped lang="sass">
.v-expansion-panel-header
  color: white
  line-height: 16px
  border-radius: 5px
.title-row
  margin-top: 30px
  margin-bottom: 30px
.block-title
  font-family: 'FSRAILBold', serif
  font-size: 18px
  margin-top: 2px
  line-height: 21px
  color: #00194d
</style>
