<template>
  <div class="pa-3">
    <v-expansion-panels v-model="panel" focusable hover>
      <v-expansion-panel>
        <v-expansion-panel-header
          @click="changeFilter()"
          hide-actions
          :color="filterHeaderColor"
          class="pa-1 pl-2 pt-2"
          :ripple="false"
        >
          <div>
            <v-icon class="mr-2" color="white">mdi-filter-outline</v-icon>
            <span>Фильтры</span>
            <v-icon class="ml-2" color="white">{{ filterIcon }}</v-icon>
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row dense>
            <DateFilter v-on:change-date="getDate" ref="dateFilter" />
          </v-row>
          <FilterButtons />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row class="ml-1 title-row">
      <h3 class="mr-auto block-title">
        <v-icon color="#ff7141" class="mr-2">mdi-trending-up</v-icon>Оперативное
        нормирование
      </h3>
      <PrintButton />
    </v-row>
    <Tabs :tableTypeArray="tablesForTabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="getParams"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import DateFilter from '../components/DateFilter';
import PrintButton from '../components/PrintButton';
import FilterButtons from '../components/FilterButtons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';
export default {
  name: 'NormalizationDepo',
  components: { Tabs, FilterButtons, PrintButton, DateFilter, DataTable },
  watch: {
    dateValue() {
      this.$refs.table.fetchTableData(this.getParams);
    }
  },
  methods: {
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    },
    getDate(dateFilter) {
      this.dateValue = dateFilter;
    }
  },
  computed: {
    filterIcon() {
      return this.$store.state.filterOpen
        ? 'mdi-chevron-up'
        : 'mdi-chevron-down';
    },
    filterHeaderColor() {
      return this.$store.state.filterOpen ? '#4D6C99' : '#30446E';
    },
    getParams() {
      let dateToParams = this.dateValue;
      if (typeof this.dateValue === 'string') {
        let dateFromFilter = new Date(this.dateValue);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return { date: dateToParams, drl: 1 };
    }
  },
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      dateValue: Math.floor(Date.now() / 1000),
      dateFilterId: 'dateNormDepo',
      url: '../../server/norm_depo.json',
      tableType: 'norm_depo',
      tablesForTabs: [
        { tabName: 'Участки', tablePath: '/control/norm/section' },
        { tabName: 'Депо', tablePath: '/control/norm/depo' },
        {
          tabName: 'Процентное соотношение локомотивов',
          tablePath: '/control/norm/ratio'
        }
      ],
      tabModel: 1,
      headersFlat: [
        { text: '№', value: 'sort_number' },
        { text: 'Депо приписки локомотивов', value: 'depot_name' },
        { text: 'Т (4-х), т.ед.', value: 's' },
        { text: 'Т (3-х), т.ед.', value: 't' },
        { text: 'Л (2-х), т.ед.', value: 'l' },
        { text: 'Всего, т.ед.', value: 'sum' }
      ],
      headers: [
        {
          text: '№',
          align: 'start',
          value: 'road_code',
          textMinify: 'mini'
        },
        {
          text: 'Депо приписки локомотивов',
          align: 'start',
          alignHeader: 'center',
          value: 'depot_name',
          textMinify: 'mini'
        },
        {
          text:
            'Потребное количество эксплуатируемого\nпарка локомотивов на следующие сутки',
          align: 'start',
          cols: 4,
          alignHeader: 'center',
          children: [
            { text: 'Т (4-х), т.ед.', value: 's', alignHeader: 'center' },
            { text: 'Т (3-х), т.ед.', value: 't', alignHeader: 'center' },
            { text: 'Л (2-х), т.ед.', value: 'l', alignHeader: 'center' },
            { text: 'Всего, т.ед.', value: 'sum', alignHeader: 'center' }
          ]
        }
      ]
    };
  }
};
</script>

<style scoped lang="sass">
.v-expansion-panel-header
  color: white
  line-height: 16px
  border-radius: 5px
.title-row
  margin-top: 30px
  margin-bottom: 30px
.block-title
  font-family: 'FSRAILBold', serif
  font-size: 18px
  margin-top: 2px
  line-height: 21px
  color: #00194d
  margin-bottom: 3px
</style>
