<template>
  <div class="mr-5">
    <p class="mb-2 filter-label">Дата</p>
    <v-menu
      v-model="fromDateMenu"
      :close-on-content-click="false"
      :nudge-right="40"
      class="mt-2"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          solo
          flat
          hide-details
          readonly
          :value="fromDateDisp"
          v-on="on"
          @click:append="showC"
        >
          <template v-slot:append>
            <v-icon color="#FF7141" class="cal-icon" @click="showC"
              >mdi-calendar</v-icon
            >
          </template>
        </v-text-field>
      </template>
      <v-date-picker
        v-model="fromDateVal"
        no-title
        @input="[closePicker(), $emit('change-date', fromDateVal)]"
        first-day-of-week="1"
      ></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'DateFilter',
  data: (vm) => ({
    isShow: false,
    fromDateVal: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    fromDateMenu: false
  }),
  computed: {
    fromDateDisp() {
      return this.formatDate(this.fromDateVal);
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    showC() {
      this.fromDateMenu = !this.fromDateMenu;
    },
    closePicker() {
      this.fromDateMenu = false;
    }
  }
};
</script>

<style scoped lang="sass">
.filter-label
  font-family: 'FSRAILBold', serif
  font-size: 16px
