<template>
  <v-btn
    class="recalc mr-3"
    :class="{ 'disable-events': isDisabled }"
    color="#4D6C99"
    ref="recalc"
    small
    dark
    depressed
    @click="recalcData"
    :ripple="false"
  >
    Пересчитать
  </v-btn>
</template>

<script>
export default {
  name: 'RecalcButton',
  data() {
    return {
      isDisabled: true
    };
  },
  methods: {
    recalcData() {
      let btnRecalc = document.querySelector('.recalc');
      btnRecalc.classList.add('disable-events');
      let btnSave = document.querySelector('.save');
      btnSave.classList.remove('disable-events');
    }
  }
};
</script>

<style scoped lang="sass">
@font-face
  font-family: FSRAILBook
  src: url('../assets/fonts/FSRAILWAYBook.otf') format('opentype')

button.recalc
  font-family: 'FSRAILBook', serif
  width: 120px
  font-size: 14px
  line-height: 17px
  letter-spacing: 0.01em
  text-transform: none
.v-btn:not(.v-btn--round).v-size--small
  height: 29px

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
