<template>
  <v-card v-resize="onResize" id="table" elevation="0">
    <v-data-table
      :headers="headersFlat"
      :items="tableData"
      dense
      class="pl-2"
      disable-pagination
      disable-filtering
      disable-sort
      hide-default-footer
      hide-default-header
      locale="ru-RU"
      :height="getTableHeight"
    >
      <template #header="{}">
        <thead class="v-data-table-header">
          <tr>
            <th
              v-for="(h, i) in headers"
              :key="i"
              :class="getAlign(h, h.textMinify)"
              :rowspan="h.children ? 1 : 3"
              :colspan="getColspan(h)"
            >
              {{ h.text }}
            </th>
          </tr>
          <tr>
            <th
              v-for="(h1, i1) in getSubHeader(headers)"
              :key="i1"
              :class="getAlign(h1, h1.textMinify)"
              :rowspan="h1.children ? 1 : 2"
              :colspan="h1.children ? h1.children.length : 1"
            >
              {{ h1.text }}
            </th>
          </tr>
          <tr>
            <th
              v-for="(h2, i2) in getSubHeader(headers, true)"
              :key="i2"
              :class="getAlign(h2)"
              :rowspan="h2.children ? 1 : 2"
              :colspan="h2.children ? h2.children.length : 1"
            >
              {{ h2.text }}
            </th>
          </tr>
        </thead>
      </template>
      <template v-slot:item="{ item }">
        <tr>
          <td
            v-for="(header, i) in headersFlat"
            :key="i"
            :style="colorCell(item, header)"
            :title="
              getTooltip(item[header.value + '_tooltip'], item[header.value]) ||
              ''
            "
          >
            <v-edit-dialog
              v-if="isEditable(item, header.editable, item[header.value])"
              :return-value.sync="item[header.value]"
              @save="changeButtons"
            >
              <span>{{ item[header.value] }}</span>
              <template v-slot:input>
                <v-text-field
                  v-model="item[header.value]"
                  single-line
                ></v-text-field>
              </template>
            </v-edit-dialog>
            <span v-else>{{
              editValue(header.value, item[header.value], item)
            }}</span>
          </td>
        </tr>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import axios from 'axios';
export default {
  name: 'DataTable',
  created() {
    this.fetchTableData();
  },
  computed: {
    getTableHeight() {
      let tableHeight = this.windowSize.y - 220;
      if (this.$store.state.filterOpen) tableHeight -= 120;
      return tableHeight;
    }
  },
  methods: {
    getAlign(header, info) {
      let cl = '';
      if (info === 'mini') {
        cl += 'smaller-text ';
      }
      if (header.alignHeader) {
        cl += 'align-text-center';
      }
      return cl;
    },
    editValue(col, value, record) {
      let val = value;
      if (this.tableType === 'norm_depo' && col === 'sort_number') {
        if (record.type === '') {
          return this.returnIndex(record);
        } else return '';
      }
      if (this.tableType === 'norm_ratio')
        if (typeof value === 'number') return Math.floor(val * 100) / 100;
      return val;
    },
    returnIndex(record) {
      let ind = 0;
      this.tableData.every(function (rec) {
        if (rec.type === '') {
          ind++;
        }
        return record !== rec;
      });
      return ind;
    },
    changeButtons() {
      let btnRecalc = document.querySelector('.recalc');
      let btnCancel = document.querySelector('.cancel');
      btnRecalc.classList.remove('disable-events');
      btnCancel.classList.remove('disable-events');
    },
    getTooltip(tooltipText, value) {
      if (tooltipText) {
        if (this.tableType === 'norm_sec') {
          let ODZ = tooltipText.split(';'),
            minODZ = Number(ODZ[0].replace(/\D+/g, '')),
            maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
            originValue,
            approveUser = '';

          if (Number(ODZ[2].replace(/\D+/g, '')))
            originValue = Number(ODZ[2].replace(/\D+/g, ''));
          if (ODZ[3].split(' = ')[1] !== '')
            approveUser = ODZ[3].split(' = ')[1];

          if (ODZ) {
            if (approveUser && originValue && value !== originValue) {
              return (
                'Данные изменены: ' +
                approveUser +
                ' с ' +
                originValue +
                ' на ' +
                value +
                '; МИН = ' +
                minODZ +
                '; МАКС = ' +
                maxODZ
              );
            } else {
              return 'МИН = ' + minODZ + '; МАКС = ' + maxODZ;
            }
          } else {
            return 'МИН = ' + minODZ + '; МАКС = ' + maxODZ;
          }
        }
        if (this.tableType === 'norm_depo') {
          return tooltipText;
        }
      } else return '';
    },
    isEditable(it, ed, value) {
      return (
        value !== undefined &&
        ed &&
        it.type !== 'S' &&
        it.type !== 'SS' &&
        it.type !== 'I'
      );
    },
    colorCell(it, col) {
      let styleString = '';
      if (+it[col.value] || it[col.value] === 0) {
        styleString += 'font-size: 14px; line-height: 16px;';
      }
      if (this.tableType === 'reg_join') {
        if (col.editable && it[col.value] === 0)
          styleString += 'background-color:#fab1b1;';
        if (col.editable) styleString += 'color:#2672ec;';
      }
      if (
        this.tableType === 'norm_sec' ||
        this.tableType === 'reg_sec' ||
        this.tableType === 'norm_depo' ||
        this.tableType === 'norm_ratio' ||
        this.tableType === 'reg_join'
      ) {
        if (col.editable) styleString += 'color:#1056EB;';
        if (
          col.value === 'tr_num' &&
          (it.type === 'S' || it.type === 'SS' || it.type === 'I')
        )
          styleString += 'color:#00194d;';
        if (it.type === 'GG' || it.type === 'SS')
          styleString += "font-family: 'FSRAILBold', serif;";
        // if (it.type === 'GG' || (it.type === 'SS' && col.value === 'long_name'))
        // styleString += 'color:#2672ec;';
        if (it.type === 'G' || it.type === 'S')
          styleString += "font-family: 'FSRAILBold', serif;";
        if (it.type === 'I') styleString += "font-family: 'FSRAILBold', serif;";
        if (
          col.value === 'long_name' ||
          col.value === 'depot_name' ||
          col.value === 'depo'
        ) {
          styleString += 'width:250px;border-right-width:1px;';
        }
        if (it[col.value + '_color']) {
          if (it[col.value + '_color'] === 'yellow')
            styleString += 'background-color:#FCFFDF';
        }

        if (it[col.value + '_tooltip'] && this.tableType !== 'norm_depo') {
          let ODZ = it[col.value + '_tooltip'].split(';'),
            minODZ = Number(ODZ[0].replace(/\D+/g, '')),
            maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
            maxODZ10 = Math.ceil(maxODZ * 1.1),
            originValue,
            approveUser = '',
            value = it[col.value];

          if (Number(ODZ[2].replace(/\D+/g, '')))
            originValue = Number(ODZ[2].replace(/\D+/g, ''));
          if (ODZ[3].split(' = ')[1] !== '')
            approveUser = ODZ[3].split(' = ')[1];

          if (ODZ) {
            if (approveUser && originValue && value !== originValue) {
              if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                styleString +=
                  'background: url(../assets/dirty.png) no-repeat 0 0; background-color: #FAC8C8; color: #2672ec; cursor: pointer;';
              } else if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                styleString +=
                  'background: url(../assets/dirty.png) no-repeat 0 0; background-color: #FAC8C8; color: #444';
              } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
                styleString +=
                  'background: url(../assets/dirty.png) no-repeat 0 0; background-color: #FCFFDF; color: #2672ec; cursor: pointer;';
              }
            } else {
              if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                styleString +=
                  'background-color: #FAC8C8; color: #2672ec; cursor: pointer;';
              } else if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                styleString += 'background-color: #FAC8C8; color: #444';
              } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
                styleString +=
                  'background-color: #FCFFDF; color: #2672ec; cursor: pointer;';
              }
            }
          }
        }
      }

      return styleString;
    },
    fetchTableData(params) {
      axios
        .get(this.url, {
          params: params || this.params
        })
        .then((response) => {
          this.tableData = response.data.data;
        });
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    getSubHeader(headers, deeper) {
      let result = [];
      headers
        .filter((i) => i.children)
        .forEach((v) => {
          result = result.concat(v.children);
        });
      if (deeper) {
        let subChild = [];
        result
          .filter((l) => l.children)
          .forEach((b) => {
            subChild = subChild.concat(b.children);
          });
        return subChild;
      }
      return result;
    },
    getColspan(col) {
      let colspan;
      if (col.cols) {
        colspan = col.cols;
      } else colspan = col.children ? col.children.length : 1;
      return colspan;
    }
  },
  props: ['headers', 'headersFlat', 'url', 'params', 'tableType'],
  data() {
    return {
      windowSize: {
        x: 0,
        y: 0
      },
      tableData: []
    };
  }
};
</script>

<style scoped lang="sass">
#table
  .v-data-table
    tbody
      tr:hover
        background-color: $color-light-blue
    td
      padding: 6px 10px
      color: $color-dark-blue
      font-size: 12px
      line-height: 14px
      border-color: $color-light-grey
      border-style: solid
      border-left-width: 0
      border-right-width: 0
      border-top-width: 0
      border-bottom-width: 1px
    th
      border-color: $color-light-grey
      white-space: pre
      font-size: $font-size-root
      line-height: $line-height-root
      font-weight: normal
      color: $color-dark-blue
      border-style: solid
      border-left-width: 0
      border-right-width: 1px
      border-top-width: 0
      border-bottom-width: 1px
      position: sticky
      padding: 8px 4px
    th.align-text-center
      text-align: center
    th.smaller-text
      font-size: 12px
      line-height: 14px
  tbody tr:nth-of-type(even)
    background-color: rgba(210, 218, 231, 0.25)
.v-sheet.v-card
  border-radius: 0 0 4px 4px
</style>
