<template>
  <div class="px-3 pt-3">
    <v-expansion-panels v-model="panel" focusable hover>
      <v-expansion-panel>
        <v-expansion-panel-header
          @click="changeFilter()"
          hide-actions
          :color="filterHeaderColor"
          class="pa-1 pl-2 pt-2"
          :ripple="false"
        >
          <div>
            <v-icon class="mr-2" color="white">mdi-filter-outline</v-icon>
            <span>Фильтры</span>
            <v-icon class="ml-2" color="white">{{ filterIcon }}</v-icon>
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row dense>
            <DateFilter v-on:change-date="getDate" ref="dateFilter" />
            <DrlFilter v-on:change-drl="getDrl" ref="drlFilter" />
          </v-row>
          <FilterButtons />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row class="ml-1 title-row">
      <h3 class="mr-5 block-title">
        <v-icon color="#ff7141" class="mr-2">mdi-trending-up</v-icon>Оперативное
        нормирование
      </h3>
      <RecalcButton />
      <SaveButton />
      <CancelButton />
      <PrintButton />
      <ExportButton :url="createExportUrl" />
    </v-row>
    <Tabs :tableTypeArray="tablesForTabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="getParams"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import DrlFilter from '../components/DrlFilter';
import DateFilter from '../components/DateFilter';
import ExportButton from '../components/ExportButton';
import PrintButton from '../components/PrintButton';
import RecalcButton from '../components/RecalcButton';
import SaveButton from '../components/SaveButton';
import CancelButton from '../components/CancelButton';
import FilterButtons from '../components/FilterButtons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';
export default {
  name: 'NormalizationSections',
  components: {
    Tabs,
    FilterButtons,
    CancelButton,
    SaveButton,
    RecalcButton,
    PrintButton,
    ExportButton,
    DateFilter,
    DrlFilter,
    DataTable
  },
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      drlValue: 1,
      requestForExport: 'method=norm_1&tab=panel-reportOP',
      dateValue: Math.floor(Date.now() / 1000),
      url: '../../server/norm_1.json',
      tablesForTabs: [
        { tabName: 'Участки', tablePath: '/control/norm/section' },
        { tabName: 'Депо', tablePath: '/control/norm/depo' },
        {
          tabName: 'Процентное соотношение локомотивов',
          tablePath: '/control/norm/ratio'
        }
      ],
      tabModel: 0,
      tableType: 'norm_sec',
      headers: [
        {
          text: 'Тяговый полигон',
          align: 'start',
          value: 'long_name',
          textMinify: 'mini',
          children: [
            { text: 'Наименование\nрасчетных участков', textMinify: 'mini' }
          ]
        },
        { text: 'Длина\n(L,км)', value: 'distance' },
        { text: 'Коэф.\nКп.гр.э', value: 'k_pgre' },
        { text: 'Коэф.\nКп.п.э.', value: 'k_pe' },
        { text: 'Кол-во\nпоездов\nN(max)', value: 'tr_num' },
        {
          text: 'В т.ч. тяжело\nвесных N\n(2-ая тяга\nСМЕТ)',
          value: 'tr_heavy_num'
        },
        { text: 'Окна,\nчас', value: 'duration' },
        {
          text: 'Потребность локомотивов',
          value: 'way',
          cols: 15,
          alignHeader: 'center',
          children: [
            {
              text: 'Грузовое движение',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 'ft' },
                { text: 'Мт\n(3-х)', value: 'ft3' },
                { text: 'Мт\n(2-х)', value: 'fl' }
              ]
            },
            {
              text: 'ПВД',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(3-х)', value: 'locl3' },
                { text: 'Мт\n(2-х)', value: 'locl' }
              ]
            },
            {
              text: 'ТОЛКАЧИ',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 'pt4' },
                { text: 'Мт\n(3-х)', value: 'pt' },
                { text: 'Мт\n(2-х)', value: 'pl' }
              ]
            },
            {
              text: 'ОПЕР, РД',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 'rt4' },
                { text: 'Мт\n(3-х)', value: 'rt' },
                { text: 'Мт\n(2-х)', value: 'rl' }
              ]
            },
            {
              text: 'ВСЕГО',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 't' },
                { text: 'Мт\n(3-х)', value: 't3' },
                { text: 'Мт\n(2-х)', value: 'l' }
              ]
            },
            {
              text: 'ИТОГО',
              alignHeader: 'center',
              children: [{ text: 'Мв', value: 'total' }]
            }
          ]
        }
      ],
      headersFlat: [
        {
          text: 'Тяговый полигон',
          align: 'start',
          sortable: false,
          value: 'long_name',
          children: [{ text: 'Наименование расчетных участков' }]
        },
        { text: 'Длина (L,км)', value: 'distance' },
        { text: 'Коэф. Кп.гр.э', value: 'k_pgre' },
        { text: 'Коэф. Кп.п.э.', value: 'k_pe' },
        {
          text: 'Кол-во поездов N(max)',
          value: 'tr_num',
          editable: true,
          tooltip: true
        },
        {
          text: 'В т.ч. тяжело весных N (2-ая тяга СМЕТ)',
          value: 'tr_heavy_num'
        },
        { text: 'Окна, час', value: 'duration' },
        { text: 'Мт (4-х)', value: 'ft' },
        { text: 'Мт (3-х)', value: 'ft3' },
        { text: 'Мт (2-х)', value: 'fl' },
        { text: 'Мт (3-х)', value: 'locl3', editable: true },
        { text: 'Мт (2-х)', value: 'locl', editable: true },
        { text: 'Мт (4-х)', value: 'pt4', editable: true },
        { text: 'Мт (3-х)', value: 'pt', editable: true },
        { text: 'Мт (2-х)', value: 'pl', editable: true },
        { text: 'Мт (4-х)', value: 'rt4', editable: true },
        { text: 'Мт (3-х)', value: 'rt', editable: true },
        { text: 'Мт (2-х)', value: 'rl', editable: true },
        { text: 'Мт (4-х)', value: 't' },
        { text: 'Мт (3-х)', value: 't3' },
        { text: 'Мт (2-х)', value: 'l' },
        { text: 'Мв', value: 'total' }
      ]
    };
  },
  watch: {
    drlValue() {
      this.$refs.table.fetchTableData(this.getParams);
    },
    dateValue() {
      this.$refs.table.fetchTableData(this.getParams);
    }
  },
  computed: {
    filterIcon() {
      return this.$store.state.filterOpen
        ? 'mdi-chevron-up'
        : 'mdi-chevron-down';
    },
    filterHeaderColor() {
      return this.$store.state.filterOpen ? '#4D6C99' : '#30446E';
    },
    getParams() {
      let dateToParams = this.dateValue;
      if (typeof this.dateValue === 'string') {
        let dateFromFilter = new Date(this.dateValue);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return { date: dateToParams, drl: this.drlValue };
    },
    createExportUrl() {
      return 'method=norm_1&tab=panel-reportOP&date=' + this.getParams.date;
    }
  },
  methods: {
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    },
    getDrl(drl) {
      this.drlValue = drl;
    },
    getDate(dateFilter) {
      this.dateValue = dateFilter;
    }
  }
};
</script>

<style scoped lang="sass">
.v-expansion-panel-header
  color: white
  line-height: 16px
  border-radius: 5px
.title-row
  margin-top: 30px
  margin-bottom: 30px
.block-title
  font-family: 'FSRAILBold', serif
  font-size: 18px
  margin-top: 2px
  line-height: 21px
  color: #00194d
</style>
