<template>
  <v-btn
    class="print mt-1 mr-3"
    color="#00194d"
    x-small
    text
    @click="printTable"
    :ripple="false"
  >
    <v-icon class="icon-side">mdi-printer</v-icon>
    Печать
  </v-btn>
</template>

<script>
export default {
  name: 'PrintButton',
  methods: {
    printTable() {
      let prtContent = document.getElementById('table');
      let WinPrint = window.open('');
      WinPrint.document.write(prtContent.innerHTML);
      WinPrint.document.close();
      WinPrint.print();
    }
  }
};
</script>

